@import "./../../styles/settings.scss";

.cards {
    position: relative;
    list-style-type: none;
    background-color: $color-light;
    height: 230px;
    width: 230px;
    margin: 20px;
    border-radius: 40px;
    cursor: pointer;
}

.cards:hover {
    border: 10px solid $color-main;
}

.name {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    line-height: 5px;
}

.mountain {
    font-family: "Calibri";
    font-size: 40px;
    font-weight: 300;
    line-height: 45px;
}

.height {
    font-family: "Inder";
    font-size: 30px;
    color: $color-main;
}

.clicked {
    background-color: $color-main;
    color: $color-dark;
}

.reached {
    color: $color-light;
    font-size: 30px;
    font-family: $font-main;
    border-top: 2px solid $color-light;
    border-bottom: 2px solid $color-light;
    padding: 20px;
}

.nameClicked {
    font-size: 24px;
    font-family: $font-text;
    padding: 1px;
}